import template from './sw-cms-el-config-cta.html.twig';

Shopware.Component.register('sw-cms-el-config-cta', {
    template,

    mixins: ['cms-element'],

    inject: ['repositoryFactory'],

    data() {
        return {
            mediaModalIsOpen: false,
            initialFolderId: null,
        };
    },

    computed: {
        ctaTitle: {
            get() {
                return this.element.config.ctaTitle.value;
            },

            set(value) {
                this.element.config.ctaTitle.value = value;
            },
        },

        ctaTitleType: {
            get() {
                return this.element.config.ctaTitleType?.value || 'h2';
            },

            set(value) {
                this.element.config.ctaTitleType.value = value;
                this.$emit('element-update', this.element);
            },
        },

        ctaTitleColor: {
            get() {
                return this.element.config.ctaTitleColor.value;
            },

            set(value) {
                this.element.config.ctaTitleColor.value = value;
            },
        },

        ctaDescription: {
            get() {
                return this.element.config.ctaDescription.value;
            },

            set(value) {
                this.element.config.ctaDescription.value = value;
            },
        },

        ctaBackgroundColor: {
            get() {
                return this.element.config.ctaBackgroundColor.value;
            },

            set(value) {
                this.element.config.ctaBackgroundColor.value = value;
            },
        },

        ctaClickable: {
            get() {
                return this.element.config.ctaClickable.value;
            },

            set(value) {
                this.element.config.ctaClickable.value = value;
                this.$emit('element-update', this.element);
            },
        },

        ctaButtonText: {
            get() {
                return this.element.config.ctaButtonText.value;
            },

            set(value) {
                this.element.config.ctaButtonText.value = value;
            },
        },

        ctaButtonUrl: {
            get() {
                return this.element.config.ctaButtonUrl.value;
            },

            set(value) {
                this.element.config.ctaButtonUrl.value = value;
            },
        },

        ctaIconShow: {
            get() {
                return this.element.config.ctaIconShow.value;
            },
            set(value) {
                this.element.config.ctaIconShow.value = value;
            },
        },

        ctaIconBorderColor: {
            get() {
                return this.element.config.ctaIconBorderColor.value;
            },

            set(value) {
                this.element.config.ctaIconBorderColor.value = value;
            },
        },

        ctaIconColor: {
            get() {
                return this.element.config.ctaIconColor.value;
            },

            set(value) {
                this.element.config.ctaIconColor.value = value;
            },
        },

        ctaIconBackgroundColor: {
            get() {
                return this.element.config.ctaIconBackgroundColor.value;
            },

            set(value) {
                this.element.config.ctaIconBackgroundColor.value = value;
            },
        },

        mediaRepository() {
            return this.repositoryFactory.create('media');
        },

        uploadTag() {
            return `cms-element-media-config-${this.element.id}`;
        },

        previewSource() {
            if (this.element?.data?.media?.id) {
                return this.element.data.media;
            }

            return this.element.config.media.value;
        },
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('cta');

            if (!this.element.config.ctaIconShow) {
                this.element.config.ctaIconShow = {
                    source: 'static',
                    value: false,
                };
            }
        },

        changeTitle(value) {
            this.element.config.ctaTitle.value = value;

            this.$emit('element-update', this.element);
        },

        changeTitleType(value) {
            this.element.config.ctaTitleType.value = value;

            this.$emit('element-update', this.element);
        },

        changeButtonUrl(value) {
            this.element.config.ctaButtonUrl.value = value;

            this.$emit('element-update', this.element);
        },

        onChangeTitleColor(value) {
            this.element.config.ctaTitleColor.value = value;

            this.$emit('element-update', this.element);
        },

        changeDescription(value) {
            this.element.config.ctaDescription.value = value;

            this.$emit('element-update', this.element);
        },

        changeBackgroundColor(value) {
            this.element.config.ctaBackgroundColor.value = value;

            this.$emit('element-update', this.element);
        },

        changeButtonText(value) {
            this.element.config.ctaButtonText.value = value;

            this.$emit('element-update', this.element);
        },

        onChangeCtaClickable(value) {
            // New method for checkbox updates
            this.element.config.ctaClickable.value = value;
            this.$emit('element-update', this.element);
        },

        onChangeButtonType(value) {
            this.$emit('element-update', this.element);
        },

        onChangeShowIcon(value) {
            this.element.config.ctaIconShow.value = value;
            this.$emit('element-update', this.element);
        },

        onChangeIconBorderColor(value) {
            this.element.config.ctaIconBorderColor.value = value;

            this.$emit('element-update', this.element);
        },

        onChangeIconBackgroundColor(value) {
            this.element.config.ctaIconBackgroundColor.value = value;

            this.$emit('element-update', this.element);
        },

        onChangeIconColor(value) {
            this.element.config.ctaIconColor.value = value;

            this.$emit('element-update', this.element);
        },

        async onImageUpload({ targetId }) {
            const mediaEntity = await this.mediaRepository.get(
                targetId,
                Shopware.Context.api
            );

            this.element.config.media.value = mediaEntity.id;

            this.updateElementData(mediaEntity);

            this.$emit('element-update', this.element);
        },

        onImageRemove() {
            this.element.config.media.value = null;

            this.updateElementData();

            this.$emit('element-update', this.element);
        },

        onCloseModal() {
            this.mediaModalIsOpen = false;
        },

        onSelectionChanges(mediaEntity) {
            const media = mediaEntity[0];
            this.element.config.media.value = media.id;

            this.updateElementData(media);

            this.$emit('element-update', this.element);
        },

        updateElementData(media = null) {
            this.$set(
                this.element.data,
                'mediaId',
                media === null ? null : media.id
            );
            this.$set(this.element.data, 'media', media);
        },

        onOpenMediaModal() {
            this.mediaModalIsOpen = true;
        },
    },
});
